import React from 'react';

function NameForm({ addName }) {
    const [firstGender, setFirstGender] = React.useState('F')
    const [firstDouble, setFirstDouble] = React.useState(false)
    const [lastDouble, setFirstLastDouble] = React.useState(false)

    const handleSubmit = (event) => {
        event.preventDefault()
        alert('A name was submitted: ' + JSON.stringify({firstGender: firstGender, firstDouble: firstDouble, lastDouble: lastDouble}))
        addName({firstGender: firstGender, firstDouble: firstDouble, lastDouble: lastDouble})
    }

    return (
        <form onSubmit={handleSubmit}>
            <label>
                Kjønn:
                <br/>
                <label>Kvinne: <input type="radio" name='first.gender' value='F' checked={firstGender === 'F'}
                                      onChange={e => setFirstGender(e.target.value)}/></label>
                <br/>
                <label>Mann: <input type="radio" name='first.gender' value='M' checked={firstGender === 'M'}
                                    onChange={e => setFirstGender(e.target.value)}/></label>
            </label>
            <br/>
            <label>
                Dobbelfornavn: <input type="checkbox" name="first.double" checked={firstDouble}
                                      onChange={e => setFirstDouble(e.target.checked)}/>
            </label>
            <br/>
            <label>
                Dobbeletternavn: <input type="checkbox" name="last.double" checked={lastDouble}
                                        onChange={e => setFirstLastDouble(e.target.checked)}/>
            </label>
            <br/>
            <input type="submit" value="Generér navn" onChange={e => handleSubmit(e)}/>
        </form>
    );
}

export default NameForm
